import React,{ useState } from 'react'
import {Container , Row , Col} from 'react-bootstrap'
import axios from 'axios'
import logo from './image/asif.png'
import './main.scss'

export const Contact = () => {


    const [ name , setName ] = useState('');
    const [ company , setCompany ] = useState('');
    const [phone , setPhone ] = useState('');
    const [ email , setEmail ] = useState('');
    const [ message , setMessage ] = useState('');

 async function MyFunction(event) {
    event.preventDefault();

    try{
        await axios.post("http://localhost:4000/message"  , {
            name , company , phone , email , message
        })
    }
    catch(err){
        if(err) throw err
    }
 }
 
    return(
        <div className='Contact'>
            <Container>
                <Row>
                    <Col id='firstSection' md={6}>
                        <img src={logo}  style={{width:'100px', display: 'block', margin: 'auto'}} />
                        <form onSubmit={MyFunction}>
                        <input type='text' name='firstname' onChange={(e)=> setName(e.target.value)} placeholder='Name' required />
                        <input type='text' name='Company' onChange={(e)=> setCompany(e.target.value)} placeholder='Company' required />
                        <input type='text' name='phone' onChange={(e)=> setPhone(e.target.value)} placeholder='Phone Number' required />
                        <input type='text' name='email' onChange={(e)=> setEmail(e.target.value)} placeholder='Email' required />
                        <input type='text' name='message' onChange={(e)=> setMessage(e.target.value)} placeholder='Your Message' required />
                        <input type='submit' value='Submit' />
                        </form>
                    </Col>
                    <Col id='secondSection' md={6}>
                        <div className='overlay'></div>
                        <div className='content'>
                            Contact Me
                        </div>
                    </Col>
                </Row>
            </Container>
        </div>
    )
}